<template>
  <table cellpadding="0" cellspacing="0" class="thead-wrapper">
    <thead v-if="!isMultiHead">
      <tr>
        <th
          v-for="(item, index) in cols"
          :style="thStyle(item, index)"
          :key="index"
        >
          <span class="td" v-if="!item.cols">{{ item.label }}</span>
        </th>
      </tr>
    </thead>
    <thead v-else>
      <tr v-for="(item, index) in cols" :key="index">
        <th
          v-for="(item2, index2) in item"
          :style="thStyle(item2, index2)"
          :key="index2"
          :colspan="item2.colspan"
          :rowspan="item2.rowspan"
        >
          <cys-checkbox
            v-if="item2.prop === '_keySelection'"
            v-model="item2.checked"
          />
          <span v-else>{{ item2.label }}</span>
        </th>
        <!-- <th v-else></th> -->
      </tr>
    </thead>
  </table>
</template>
<script>
import Emitter from "../../mixins/emitter.js";
export default {
  name: "CysTableColum",
  componentName: "CysTableColum",
  mixins: [Emitter],
  props: {
    options: {
      type: Object,
      default: {}
    }
  },
  data() {
    return {
      fixed: {
        Lcols: [], // 左边固定列
        LD: [], // 左边边固定列数据
        Rcols: [], // 右边边固定列
        RD: [] // 右边边固定列数据
      },
      isMultiHead: false
    };
  },
  computed: {
    thStyle() {
      return function(item, index) {
        if (item.style) {
          return item.style;
        } else {
          return "";
        }
      };
    },
    cols() {
      let cols = (this.options.thead.column && this.options.thead.column) || [];
      let tKey = [];

      if (JSON.stringify(cols).indexOf("cols") > 0) {
        // 多表头
        this.isMultiHead = true;
        let mut = [];

        cols = this.setColIndex(cols, true);

        cols = this.setColSelection(cols, true);

        cols = cols.map((col, index) => {
          col.forEach((col2, index2) => {
            if (col2.prop) {
              mut.push(col2);
            }
          });
          return col;
        });

        this.setColFixed(cols, true);

        tKey = mut;
        
      } else {
        // 单表头

        cols = this.setColIndex(cols);

        cols = this.setColSelection(cols);

        cols = this.setColFixed(cols);

        tKey = cols;

      }

      this.dispatchByMethod("CysTable", "tdStyle", tKey);

      return cols;
    }
  },
  created() {
    this.$on("getFixedDate", this.getFixedDate);
  },
  methods: {
    setColIndex(cols, m) {
      if (this.options.isIndex) {
        if (m) {
          let fcol = {
            label: this.options.indexLabel || "序号",
            prop: "_keyIndex",
            fixed: "left",
            style: {
              width: this.options.indexWidth || "80px"
            },
            rowspan: cols.length,
            colspan: 1
          };
          if (JSON.stringify(cols[0]).indexOf("_keyIndex") < 0) {
            cols[0].unshift(fcol);
          }
        } else {
          cols = [
            {
              label: this.options.indexLabel || "序号",
              prop: "_keyIndex",
              fixed: "left",
              style: {
                width: this.options.indexWidth || "80px"
              }
            }
          ].concat(cols);
        }
      }
      return cols;
    },
    setColSelection(cols, m) {
      if (this.options.isSelection) {
        if (m) {
          let fcol = {
            label: "",
            prop: "_keySelection",
            fixed: "left",
            checked: false,
            style: {
              width: this.options.indexSelectionWidth || "60px"
            },
            rowspan: cols.length,
            colspan: 1
          };
          if (JSON.stringify(cols[0]).indexOf("_keyIndex") < 0) {
            cols[0].unshift(fcol);
          }
        } else {
          cols = [
            {
              label: "",
              prop: "_keySelection",
              fixed: "left",
              checked: false,
              style: {
                width: this.options.indexSelectionWidth || "60px"
              }
            }
          ].concat(cols);
        }
      }
      return cols;
    },
    setColFixed(cols, m) {
      if (JSON.stringify(cols).indexOf("fixed") > 0) {
        const mid = [];
        this.fixed.Rcols = [];
        this.fixed.Lcols = [];

        if (m) {
          const cee = cols => {
            cols.forEach((col, index) => {
              if (col instanceof Array) {
                cee(col);
              } else {
                if (col.fixed === "right") {
                  this.fixed.Rcols.push(col);
                } else if (col.fixed === "left") {
                  this.fixed.Lcols.push(col);
                }
              }
            });
          };
          cee(cols);
        } else {
          cols.forEach((col, index) => {
            if (col.fixed === "right") {
              cols.slice(index, 1);
              this.fixed.Rcols.push(col);
            } else if (col.fixed === "left") {
              cols.slice(index, 1);
              this.fixed.Lcols.push(col);
            } else {
              mid.push(col);
            }
          });
          cols = []
            .concat(this.fixed.Lcols)
            .concat(mid)
            .concat(this.fixed.Rcols);
        }
      }
      return cols;
    },
    selectAll() {},
    getFixedDate(conf) {
      const table = conf.table;
      if (!table) return;
      const thead = table.querySelector(".thead-wrapper");
      this.fixed.RD = [];
      this.fixed.LD = [];
      const fR = () => {
        const cols = this.fixed.Rcols;
        const data = this.options.tbody.data;
        const cl = [];
        for (let i = 0; i < cols.length; i++) {
          const col = cols[i];
          const th = thead.rows[0].cells[data.length - (cols.length - 1 - i)];
          const wd = th.offsetWidth;
          const hd = th.offsetHeight;
          th.innerHTML = "";
          const tcl = [];
          for (let j = 0; j < data.length; j++) {
            tcl.push(data[j]);
          }
          this.fixed.RD.push({
            th: col,
            wd: wd,
            hd: hd,
            td: tcl
          });
        }
      };
      const FL = () => {
        const cols = this.fixed.Lcols;
        const data = this.options.tbody.data;
        const cl = [];
        for (let i = 0; i < cols.length; i++) {
          const col = cols[i];
          const th = thead.rows[0].cells[i];
          const wd = th.offsetWidth;
          const hd = th.offsetHeight;
          th.innerHTML = "";
          const tcl = [];
          for (let j = 0; j < data.length; j++) {
            tcl.push(data[j]);
          }
          this.fixed.LD.push({
            th: col,
            wd: wd,
            hd: hd,
            td: tcl
          });
        }
      };
      fR();
      FL();
      this.dispatchByMethod("CysTable", "setFixed", this.fixed);
    }
  }
};
</script>



