<template>
  <div>
    <div
      class="cys-table"
      :class="[
        {
          'cys-table--fit': fit,
          'cys-table--striped': striped,
          'cys-table--border': border
        }
      ]"
      :id="tableGuid"
    >
      <div class="cys-table-wrapper">
        <div class="cys-table-scwrapper">
          <div class="cys-table-header"><Colum :options="options" /></div>
          <div class="cys-table-tbody">
            <div :class="getTodyWrapClass">
              <table
                cellpadding="0"
                cellspacing="0"
                class="tbody-wrapper"
              >
                <tbody v-if="options.tbody.data.length > 0">
                  <tr v-for="(item, index) in options.tbody.data" :key="index">
                    <td v-if="item.expand" :colspan="item.colspan">
                      <slot :name="item.slotName" :row="item"></slot>
                    </td>
                    <td
                      v-for="(item2, index2) in cols"
                      :key="index2"
                      :style="{
                        width:
                          item2.style && item2.style.width
                            ? item2.style.width
                            : ''
                      }"
                      v-else
                    >
                      <slot
                        v-if="item2.slotName"
                        :name="item2.slotName"
                        :row="item"
                      ></slot>
                      <span v-else-if="item2.prop === '_keySelection'"></span>
                      <span v-else-if="item2.expand"
                        ><i
                          @click="expandChange(item)"
                          class="cysicon icon-arrow_right"
                        ></i>
                        {{ item[item2.prop] }}</span
                      >
                      <span v-else> {{ item[item2.prop] }}</span>
                    </td>
                  </tr>
                </tbody>
                <tbody v-else>
                  <tr>
                    <td class="center">无数据</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
        <div class="cys-table-fixed-right">
          <div
            class="cys-table-fixed-col"
            v-for="(col, index) in fixed.RD"
            :key="index"
            :style="{ width: col.wd + 'px' }"
          >
            <table cellpadding="0" cellspacing="0">
              <thead>
                <tr :style="{ height: col.hd + 'px' }">
                  <th v-if="col.th.prop === '_keySelection'">
                    <cys-checkbox v-model="col.th.checked" />
                  </th>
                  <th v-else>{{ col.th.label }}</th>
                </tr>
              </thead>
            </table>
            <table cellpadding="0" cellspacing="0">
              <tbody>
                <tr v-for="(item, index2) in col.td" :key="index2">
                  <td>
                    <slot
                      v-if="col.th.slotName"
                      :name="col.th.slotName"
                      :row="item"
                    ></slot>
                    <span v-else-if="col.th.expand"
                      ><i
                        @click="expandChange(item)"
                        class="cysicon icon-arrow_right"
                      ></i>
                      {{ item[col.th.prop] }}</span
                    >
                    <span v-else-if="col.th.prop === '_keySelection'"
                      ><cys-checkbox v-model="item[col.th.prop]"
                    /></span>
                    <span v-else>{{ item[col.th.prop] }}</span>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
        <div class="cys-table-fixed-left">
          <div
            class="cys-table-fixed-col"
            v-for="(col, index) in fixed.LD"
            :key="index"
            :style="{ width: col.wd + 'px' }"
          >
            <table cellpadding="0" cellspacing="0">
              <thead>
                <tr :style="{ height: col.hd + 'px' }">
                  <th v-if="col.th.prop === '_keySelection'">
                    <cys-checkbox
                      v-model="checkedAll"
                      @change="callSelectAll"
                      :indeterminate="!checkedAll && indeterminate"
                    />
                  </th>
                  <th v-else>{{ col.th.label }}</th>
                </tr>
              </thead>
            </table>
            <table cellpadding="0" cellspacing="0">
              <tbody>
                <tr v-for="(item, index2) in col.td" :key="index2">
                  <td>
                    <slot
                      v-if="col.th.slotName"
                      :name="col.th.slotName"
                      :row="item"
                    ></slot>
                    <span v-else-if="col.th.expand"
                      ><i
                        @click="expandChange(item)"
                        class="cysicon icon-arrow_right"
                      ></i>
                      {{ item[col.th.prop] }}</span
                    >
                    <span v-else-if="col.th.prop === '_keySelection'"
                      ><cys-checkbox
                        v-model="item[col.th.prop]"
                        @change="isSelectionChecked"
                    /></span>
                    <span v-else>{{ item[col.th.prop] }}</span>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
      <div class="cys-table-tfooter"></div>
      <!-- <div class="cys-table-styles" v-html="thw"></div> -->
      <cys-loading v-if="options.isLoading" :plate="'white'"></cys-loading>
    </div>
    <cys-pagination
      v-if="options.isPagination"
      :total="options.tbody.total"
      :layout="'total, sizes, prev, pager, next, jumper'"
      @current-change="currentChange"
      @size-change="sizeChange"
    ></cys-pagination>
  </div>
</template>
<script>
import Scroll from "../../utils/scroll";
import Colum from "./table-colum";
import Emitter from "../../mixins/emitter.js";
export default {
  name: "CysTable",
  componentName: "CysTable",
  components: {
    Colum
  },
  mixins: [Emitter],
  props: {
    fit: {
      type: Boolean,
      default: true
    },
    striped: {
      type: Boolean,
      default: false
    },
    border: {
      type: Boolean,
      default: false
    },
    height: {
      type: String,
      default: "auto"
    },
    minWidth: {
      type: String,
      default: "auto"
    },
    rowKey: {
      type: String,
      default: ""
    },
    options: { type: Object, default: {} }
  },
  computed: {
    isTheadShow() {
      return typeof this.options.thead.show === "undefined"
        ? true
        : this.options.thead.show;
    },

    getTodyWrapClass() {
      let cs = "cys-table-body-wrapper";
      if (this.height) {
        cs += " overy";
      }
      if (this.width) {
        cs += " horizontal";
      }
      return cs;
    },
    dynamicSlotName() {
      const name = this.options.thead.column.map(r =>
        r.slotName ? r.slotName : ""
      );
      return "bb";
    },
    tableGuid() {
      const guid = this.cguid();
      this.guid = `table-${guid}`;
      return this.guid;
    }
  },
  data() {
    return {
      parameter: {},
      page: {
        current: 1,
        size: 10
      },
      cols: [],
      fixed: {},
      thw: [],
      guid: 0,
      rowKeys: [],
      checkedAll: false,
      indeterminate: false
    };
  },
  created() {
    this.getTableData();
    this.windowResize();
  },
  methods: {
    callSelectAll(val) {
      this.fixed.LD[0].td = this.fixed.LD[0].td.map(r => {
        r._keySelection = val;
        return r;
      });
      this.checkedAll = val;
    },
    isSelectionCheckedAll() {
      if (this.fixed.LD[0].td.every(r => r._keySelection === true)) {
        this.checkedAll = true;
        this.indeterminate = false;
      } else if (this.fixed.LD[0].td.every(r => r._keySelection === false)) {
        this.checkedAll = false;
        this.indeterminate = false;
      } else {
        this.checkedAll = false;
        this.indeterminate = true;
      }
    },
    isSelectionChecked() {
      this.isSelectionCheckedAll();
    },
    setFixed(fixed) {
      this.fixed = fixed;
    },
    expandKeys(item) {
      let isOpen = false;
      if (this.rowKeys.every(v => v !== item[this.rowKey])) {
        this.rowKeys.push(item[this.rowKey]);
        const index = this.getRowIndex(item);
        this.options.tbody.data.splice(index + 1, 0, {
          expand: true,
          slotName: "expand",
          row: item,
          colspan: this.cols.length
        });
        isOpen = true;
      } else {
        this.rowKeys = this.rowKeys.filter(v => v !== item[this.rowKey]);
        const index = this.getRowIndex(item);
        this.options.tbody.data.splice(index + 1, 1);
        isOpen = false;
      }
    },
    toggleRowExpansion(item, isOpen) {
      if (isOpen) {
        if (this.rowKeys.every(v => v !== item[this.rowKey])) {
          this.rowKeys.push(item[this.rowKey]);
          const index = this.getRowIndex(item);
          this.options.tbody.data.splice(index + 1, 0, {
            expand: true,
            slotName: "expend",
            row: item,
            colspan: this.cols.length
          });
        }
      } else {
        this.rowKeys = this.rowKeys.filter(v => v !== item[this.rowKey]);
        const index = this.getRowIndex(item);
        this.options.tbody.data.splice(index + 1, 1);
      }
      this.$emit("expand-change", isOpen);
      this.$forceUpdate();
    },
    getRowIndex(item) {
      let index = 0;
      this.options.tbody.data.forEach((row, ii) => {
        if (row[this.rowKey] === item[this.rowKey]) {
          index = ii;
        }
      });
      return index;
    },
    expandChange(item) {
      const isOpen = this.expandKeys(item);
      this.$emit("expand-change", isOpen);
      this.$forceUpdate();
    },
    tdStyle(cols) {
      this.cols = cols;
      // const arr = [];
      // cols.forEach((item, index) => {
      //   if (item.style && item.style.width) {
      //     const w = parseInt(item.style.width);
      //     arr.push(
      //       `#${this.guid} tr td:nth-child(${index + 1}){ width:${w}px; }`
      //     );
      //   }
      // });
      // this.thw = arr.length > 0 ? `<style>${arr.join("")}</style>` : "";
    },
    cguid() {
      return "xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx".replace(/[xy]/g, function(
        c
      ) {
        var r = (Math.random() * 16) | 0,
          v = c == "x" ? r : (r & 0x3) | 0x8;
        return v.toString(16);
      });
    },
    getOptions() {
      const opt = {
        thead: {
          column: [],
          show: true
        },
        tbody: {
          total: 0,
          data: []
        },
        method: {
          interface: () => {}, // 调用表格数据接口方法
          parmas: {}, // 参数
          cVal: 0, // 接口返回参数值 默认0
          cKey: "errorCode", // 接口返回参数key 默认errorCode
          dKey: "data", // 接口返回参数key 默认errorCode
          tKey: "total",
          pageNumKey: "pageNum",
          pageSizeKey: "pageSize",
          failed: () => {}, // 接口失败回调
          success: () => {} // 接口成功回调
        },
        isPagination: false
      };
      if (JSON.stringify(this.options) === "{}") {
        this.options = opt;
      } else {
        Object.keys(opt).forEach(key => {
          if (
            typeof this.options[key] === "undefined" ||
            JSON.stringify(this.options[key]) === "{}"
          ) {
            this.options[key] = opt[key];
          } else {
            this.options[key] =
              typeof this.options[key] === "object"
                ? Object.assign({}, opt[key], this.options[key])
                : this.options[key];
          }
        });
      }
      return this.options;
    },
    async getTableData() {
      const opts = this.getOptions();
      const method = opts.method.interface;
      const cKey = opts.method.cKey;
      const cVal = opts.method.cVal;
      const tKey = opts.method.tKey;
      const success = opts.method.success;
      const failed = opts.method.failed;
      const dKey = opts.method.dKey;

      if (opts.isLoading) opts.isLoading = true;
      console.log("this.getParameter()", this.getParameter());
      const respone = await method(this.getParameter());

      if (respone[cKey] * 1 === cVal * 1) {
        const data = respone[dKey];
        if (opts.isPagination) {
          this.options.tbody.total = data.total * 1;
          this.options.tbody.data = data.list;
        } else {
          this.options.tbody.total = 0;
          this.options.tbody.data = data;
        }

        if (opts.isLoading) opts.isLoading = false;

        if (this.options.isIndex) {
          this.options.tbody.data = this.options.tbody.data.map((r, index) => {
            r._keyIndex = (this.page.current - 1) * this.page.size + index + 1;
            return r;
          });
        }

        if (this.options.isSelection) {
          this.options.tbody.data.map((r, index) => {
            r._keySelection = false;
            return r;
          });
        }

        this.$forceUpdate();
        this.$nextTick(() => {
          const tWrapper = document.querySelector("#" + this.guid);
          if (tWrapper) {
            this.broadcast("CysTableColum", "getFixedDate", {
              table: tWrapper
            });
            this.Scroll = new Scroll({
              wrapper: tWrapper,
              height: this.height,
              minWidth: this.minWidth
            });
          }
        });
        success && success(respone);
      } else {
        if (opts.isLoading) opts.isLoading = true;
        failed && failed(respone);
      }
    },
    windowResize() {
      var resizeTimer = null;
      window.addEventListener(
        "resize",
        () => {
          if (resizeTimer) {
            if (this.Scroll) {
              this.Scroll.windRisize();
            }
            this.broadcast("CysTableColum", "getFixedDate", {
              table: document.querySelector("#" + this.guid)
            });
          }
          resizeTimer = setTimeout(function() {
            console.log("window resize");
          }, 300);
        },
        false
      );
    },
    currentChange(val) {
      this.page.current = val;
      const opts = this.getOptions();
      const pageNumKey = opts.method.pageNumKey;
      const pageSizeKey = opts.method.pageSizeKey;
      const paper = {};
      paper[pageNumKey] = this.page.current;
      paper[pageSizeKey] = this.page.size;
      this.setParameter(paper);
      this.getTableData();
    },
    sizeChange(val) {
      this.page.size = val;
      const opts = this.getOptions();
      const pageNumKey = opts.method.pageNumKey;
      const pageSizeKey = opts.method.pageSizeKey;
      const paper = {};
      paper[pageNumKey] = this.page.current;
      paper[pageSizeKey] = this.page.size;
      this.setParameter(paper);
      this.getTableData();
    },
    setParameter(parmas) {
      const opts = this.getOptions();
      window.__storevueappdate__state_formData
        ? window.__storevueappdate__state_formData
        : {};
      this.parameter = Object.assign(
        {},
        opts.method.parmas,
        parmas,
        window.__storevueappdate__state_formData
      );
    },
    getParameter() {
      return this.parameter;
    }
  }
};
</script>
<style lang="stylus">
@import '../../styles/variable';

.cys-table {
  display : flex;
  position: relative;
  padding: 0 0 0 0;
  flex-direction: column;
  .cys-table-wrapper {
    overflow: hidden;
  }
  

  .center {
    text-align center;
  }

  .td {
    padding 0;
    display inline-block
  }

  .cys-table-cols {
    border-top 1px solid $--table-border-color;
    .kt {
      padding 10px;
      margin 0;
      border-bottom 1px solid $--table-border-color;
    }
    th,td {
      padding 0;
    }
  }
  &.cys-table--border {
    .cys-table-fixed-right {
      th {
        color: $--table-tbody-th;
        border-bottom 1px solid $--table-border-color;
      }
      td {
        color: $--table-tbody-td;
      }
      border-right 1px solid $--table-border-color;
      top 1px;
      th,td {
        border-left 1px solid $--table-border-color!important;
      }
      tr:last-child {
        td {
          border-bottom 1px solid $--table-border-color;
        }
      }

    }
    .cys-table-fixed-left {
      th {
        color: $--table-tbody-th;
        border-bottom 1px solid $--table-border-color;
      }
      td {
        color: $--table-tbody-td;
      }
      border-right 1px solid $--table-border-color;
       top 1px;
       th,td {
        border-left 1px solid $--table-border-color!important;
      }
      tr:last-child {
        td {
          border-bottom 1px solid $--table-border-color;
        }
      }
    }
  }
  .cys-table-fixed-right {
    th {
      font-weight: normal;
      color: $--table-thead-th;
      background-color: $--table-th-background;
      border-bottom 0px
    }
    td {
      color: $--table-tbody-td;
    }
    background-color $--table-background-color
    height 100%;
    position absolute;
    right 0;
    top 0px;
    border-left 0;
    .cys-table-fixed-col {
      display inline-block
    }
    td {
      border-bottom 1px solid $--table-border-color;
    }
    tr:last-child {
      td {
        border-bottom none;
      }
    }
  }

  .cys-table-fixed-left {
    th {
      font-weight: normal;
      color: $--table-thead-th;
      background-color: $--table-th-background;
    }
    td {
      color: $--table-tbody-td;
    }
    background-color $--table-background-color
    height 100%;
    position absolute;
    left 0;
    top 0px;
    border-left 0;
    .cys-table-fixed-col {
      display inline-block
    }
    th,td {
      border-bottom 1px solid $--table-border-color;
    }
    tr:last-child {
      td {
        border-bottom none;
      }
    }
  }

  .cys-table-scwrapper{
    min-width: 1000px;
    background $--table-background-color
  }

  table {
    table-layout: fixed;
    width 100%;
    tr {
      width 100%;
      th,td {
        width: auto;
        text-align: left;
        padding: 15px 10px;
        &.pd0 {
          padding 0;
        }
      }
    }
  }
  .cys-table-header {
    th {
      font-weight: normal;
      color: $--table-thead-th;
      background-color: $--table-th-background;
    }
  }

  .cys-table-bar-box {
    &.hzt {
      width: 100%;
      height: $--table-bar-box-width;
      bottom: $--table-bar-button-size;
      left: 0;
      top auto;
      display: none;
      .cys-table-bar-box-drop {
        height: 100%;
      }
    }
    background: $--table-bar-box-background;
    position absolute;
    right 0;
    bottom 1px;
    width: $--table-bar-box-width;
    height 100%;
    .cys-table-bar-box-drop {
      position absolute;
      top 0;
      width 100%;
      background: $--table-bar-box-drop-background;
    }
  }

  .cys-table-tbody {
    position relative;
    .cys-table-body-wrapper {
      &.overy {
        overflow hidden;
        position relative;
      }
    }
    flex-grow : 1;
    tr{
      td {
        border-bottom: 1px solid $--table-border-color;
        color: $--table-tbody-td;
      }
      &:last-child{
        td {
          border-bottom: 0;
        }
      }
    }
  }

  &.cys-table--striped {
    .cys-table-tbody {
      tr {
        &:nth-child(odd)
        {
          background: $--table-tbody-td-odd;
        }
        &:nth-child(even)
        {
          background: $--table-tbody-td-even;
        }
      }
    }
  }

  &.cys-table--border{
    .cys-table-scwrapper {
      border: 1px solid $--table-border-color;
      border-left: 0;
      .cys-table-header {
        th {
          border-bottom: 1px solid $--table-border-color;
          font-weight: normal;
        }
      }
      th,td {
        border-left: 1px solid $--table-border-color;
      }
    }
  }


}
</style>



