<template>
  <div class="cys-form-search">
    <div
      :class="
        direction === 'inline'
          ? 'cys-form-search-wrap'
          : 'cys-form-search-wrap vertical'
      "
    >
      <div class="cys-form-search-el">
        <div
          class="cys-form-search-item"
          :key="index"
          v-for="(form, index) in data"
        >
          <component
            @change="change"
            :is="getComponent(form.type)"
            :option="form"
          ></component>
        </div>
      </div>
      <div class="cys-form-search-btn" :style="bW">
        <cys-button
          :key="index"
          v-for="(btn, index) in btns"
          @click="btnClick(btn)"
          >{{ btn.label }}</cys-button
        >
      </div>
    </div>
  </div>
</template>
<script>
import Emitter from "../../mixins/emitter.js";
import Input from "./input";
import Select from "./select";
import Date from "./date";
const components = {
  Input: Input,
  Select: Select,
  Date: Date
};
export default {
  name: "CysFormSearch",
  componentName: "CysFormSearch",
  components: components,
  mixins: [Emitter],
  props: {
    btns: {
      type: Array,
      default: () => {
        return [];
      }
    },
    data: {
      type: Array,
      default: () => {}
    },
    direction: {
      type: String,
      default: "inline"
    },
    pointer: {
      type: String,
      default: ""
    },
    parent: {
      type: String,
      default: ""
    }
  },
  created() {
    console.log(this.parent, "parentName");
  },
  computed: {
    bW() {
      let wd = 0;
      this.btns.forEach(el => {
        wd += parseInt(el.width || 6) + 10;
      });
      return `{width:${wd}px`;
    }
  },
  data() {
    return {
      formData: {}
    };
  },
  methods: {
    getComponent(type) {
      if (type.indexOf("date") > -1) {
        return "Date";
      } else {
        console.log("cccee", type.substr(0, 1).toUpperCase() + type.substr(1));
        return type.substr(0, 1).toUpperCase() + type.substr(1);
      }
    },
    change(val) {
      this.formData = Object.assign({}, this.formData, val);
      console.log(this.formData);
    },
    btnClick(btn) {
      if (btn.action && typeof btn.action === "function") {
        btn.action(this.formData);
      }
      let parent = null;
      if (this.parent) {
        parent = this.dispatchComponentByName(this.parent);
      } else {
        parent = this.$parent;
      }
      const table = parent.$refs[this.pointer || "cys-ref-table"];
      window.__storevueappdate__state_formData = {};
      window.__storevueappdate__state_formData = this.formData;
      table.currentChange(1);
    }
  }
};
</script>
<style lang="stylus" scoped>
.cys-form-search {
    .cys-form-search-wrap {
      display flex
      .cys-form-search-el {
        flex 1
        .cys-form-search-item {
            display inline-block;
            margin-right 10px;
            margin-bottom 10px;
            .cys-form--inline {
                display flex;
                align-items center;
                /deep/ .cys-form-label {
                    margin-right 10px;
                }
            }
        }
        &.vertical {
          .cys-form-search-item {
            display block;
            margin-right 0px;
          }
        }
      }
      .cys-form-search-btn {
        .cys-button {
          margin-left 10px
        }
      }
    }
}
</style>



